.concentration {
  width: 975px;
  height: 269px;
}
.concentration .topConcentration {
  width: 975px;
  height: 37px;
}
.concentration .topConcentration .cod {
  float: left;
  text-align: center;
  width: 137px;
  height: 37px;
}
.concentration .topConcentration .cod p {
  font-size: 16px;
  line-height: 37px;
  letter-spacing: 1px;
  color: #ffffff;
  text-shadow: 0 0 30px #f4faf5;
}
.concentration .topConcentration .qishu {
  float: right;
  width: 180px;
  height: 29px;
}
.concentration .topConcentration .qishu .one {
  margin-top: 10px;
  float: left;
  text-align: center;
  line-height: 29px;
  font-size: 14px;
  letter-spacing: 1px;
  color: #00ffff;
  width: 88px;
  height: 29px;
}
.concentration .topConcentration .qishu .three {
  margin-top: 10px;
  float: right;
  width: 88px;
  height: 29px;
  text-align: center;
  line-height: 29px;
  font-size: 14px;
  letter-spacing: 1px;
  color: #42b6fe;
}
.districtRow {
  width: 100%;
  height: 222px;
  margin-top: 10px;
  border-style: solid;
  border-color: transparent;
  border-width: 200px 310px 35px 65px;
  border-image: url(/images/imgs/onescreen/框.png);
  border-image-slice: 200 310 35 65 fill;
  padding: 0;
}
.districtRow .sbox {
  height: 210px;
  margin-top: -200px;
  margin-left: -65px;
  margin-right: -330px;
  margin-bottom: -15px;
}
.districtRow .sbox .oneBox:nth-child(1) {
  margin: 25px 0 0 47px;
  border: 1px dashed #0090ff;
}
.districtRow .sbox .oneBox:nth-child(2) {
  margin: 25px 0 0 30px;
  border: 1px dashed #0090ff;
  border-style: dashed none dashed dashed;
}
.districtRow .sbox .oneBox:nth-child(3) {
  border: 1px dashed #0090ff;
  border-style: dashed none dashed none;
}
.districtRow .sbox .oneBox:nth-child(4) {
  border: 1px dashed #0090ff;
  border-style: dashed none dashed none;
}
.districtRow .sbox .oneBox:nth-child(5) {
  border: 1px dashed #0090ff;
  border-style: dashed dashed dashed none;
}
.districtRow .sbox .oneBox {
  display: inline-block;
  position: relative;
  width: 172px;
  height: 180px;
}
.districtRow .sbox .oneBox .codCircle {
  position: absolute;
  top: 30px;
  left: 22px;
}
.districtRow .sbox .oneBox p {
  position: absolute;
  font-size: 14px;
  color: #ffffff;
}
.districtRow .sbox .oneBox span {
  position: absolute;
  left: -2%;
  top: 40%;
  font-size: 16px;
  color: #0090ff;
  letter-spacing: 2px;
}
.spanA {
  position: absolute;
  top: 90% !important;
  left: 35% !important;
  margin-top: -13px;
}
.spanANew {
  position: absolute;
  top: 90% !important;
  left: 0 !important;
  margin-top: -13px;
  width: 100%;
  text-align: center;
}
.spanAA {
  position: absolute;
  top: 50% !important;
  left: 35% !important;
}
.spanAAA {
  position: absolute;
  top: 40% !important;
  left: 30% !important;
}
